/* Otros estilos para mejorar la apariencia del formulario según tus necesidades */
html {
  box-sizing: border-box;
  font-size: 62.5%;
  background-color: #e8e8e8;
  /* 1rem = 10px */
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  height: 100%;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 1.6rem;
}

p {
  font-size: 1.4rem;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  font-size: 1.2rem;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.btns__App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin-top: 5rem;
}

.btn__App {
  background-color: #000;
  width: 25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  padding: 1rem 0;
}
.btn__logo img {
  width: 3rem;
  margin-right: 1rem;
}
.btn__texto p {
  margin: 0;
  color: #fff;
  font-size: 1.8rem;
  line-height: 1;
}
.btn__texto span {
  font-size: 1rem;
}

@media (min-width: 768px) {
  .btn__App {
    width: 40rem;
  }
  .app__contenido h2 {
    margin: 0;
    font-size: 5rem;
    margin-bottom: 1.5rem;
  }
  .app__contenido p {
    font-size: 3rem;
  }
  .btn__logo img {
    width: 5rem;
    margin-right: 1rem;
  }
  .btn__texto p {
    margin: 0;
    color: #fff;
    font-size: 2.5rem;
    line-height: 1;
  }
  .btn__texto span {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .btns__App {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    margin-top: 5rem;
  }
}
.campo {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1rem;
  margin-top: 2rem;
}

input {
  padding: 1rem;
  outline: #707070 1px solid;
  border-radius: 0.5rem;
  border-style: none;
}

input[type=file] {
  width: 100%;
}

select, textarea {
  padding: 1rem;
  outline: #707070 1px solid;
  border-radius: 0.5rem;
  border-style: none;
}

input[type=checkbox] {
  outline: none;
}

select:hover, textarea:hover, input:hover {
  outline: #0a7bec 1px solid;
}

select:active, textarea:active, input:active {
  outline: #0a7bec 1px solid;
}

input::placeholder {
  color: #707070;
  font-size: 1.4rem;
}

@media (min-width: 1024px) {
  .campo {
    margin-top: 1rem;
  }
  input {
    padding: 0.5;
  }
}
label {
  font-size: 1.4rem;
}

/* tarjetas */
.tarjeta {
  background-color: #fff;
  margin: 5rem;
  position: fixed;
  bottom: 0rem;
  border-radius: 1rem;
  z-index: 1;
  display: flex;
  align-items: center;
  padding: 2rem 3rem;
  gap: 1rem;
  width: 20rem;
}

.tarjeta img {
  width: 3rem;
}

.boton__verde {
  background-color: #0C6432;
  font-size: 1.4rem;
  padding: 0.8rem 1.5rem; /* Reducimos el valor del padding para hacer el botón más delgado */
  font-weight: 400;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 0.5rem;
  font-family: "Montserrat", sans-serif;
}
.boton__verde:hover {
  cursor: pointer;
  background-color: #07361b;
}
@media (min-width: 768px) {
  .boton__verde {
    width: auto;
    display: inline-block;
  }
}

.boton__negro {
  background-color: #13161C;
  font-size: 1.4rem;
  padding: 0.8rem 1.5rem; /* Reducimos el valor del padding para hacer el botón más delgado */
  font-weight: 400;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 0.5rem;
  font-family: "Montserrat", sans-serif;
}
.boton__negro:hover {
  cursor: pointer;
  background-color: black;
}
@media (min-width: 768px) {
  .boton__negro {
    width: auto;
    display: inline-block;
  }
}

.boton__blanco {
  background-color: #fff;
  font-size: 1.4rem;
  padding: 0.8rem 1.5rem; /* Reducimos el valor del padding para hacer el botón más delgado */
  font-weight: 400;
  display: block;
  width: 100%;
  text-align: center;
  color: #000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 0.5rem;
  font-family: "Montserrat", sans-serif;
}
.boton__blanco:hover {
  cursor: pointer;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .boton__blanco {
    width: auto;
    display: inline-block;
  }
}

.boton__rojo {
  background-color: rgb(194, 29, 29);
  font-size: 1.4rem;
  padding: 0.8rem 1.5rem; /* Reducimos el valor del padding para hacer el botón más delgado */
  font-weight: 400;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 0.5rem;
  font-family: "Montserrat", sans-serif;
}
.boton__rojo:hover {
  cursor: pointer;
  background-color: #961616;
}
@media (min-width: 768px) {
  .boton__rojo {
    width: auto;
    display: inline-block;
  }
}

.boton__azul {
  background-color: #106ECC;
  font-size: 1.4rem;
  padding: 0.8rem 1.5rem; /* Reducimos el valor del padding para hacer el botón más delgado */
  font-weight: 400;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 0.5rem;
