/* Banner */
.banner {
  display: none;
  margin: 0;
  padding: 0.5rem;
  width: 100%;
  background-color: #0C6432;
}
.banner__texto {
  color: #fff;
  font-weight: bold;
}

@media (min-width: 480px) {
  .banner {
    display: block;
  }
} /* header Candidato */
.candidato__header__contenido {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 80%;
}

@media (min-width: 768px) {
  .candidato__header__contenido {
    z-index: 1;
  }
}
.candidato__header {
  height: 6rem;
  background-color: #13161C;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.candiadato__logo a {
  color: #fff;
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: var(--bold);
  font-size: 2.3rem;
}

.logoMobile {
  display: block;
}

.logoDesktop {
  display: none;
}

@media (min-width: 1200px) {
  .logoMobile {
    display: none;
  }
  .logoDesktop {
    display: block;
  }
}
.header__izquierda {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.candidato__buscar {
  display: flex;
  align-items: center;
  gap: 1rem;
  background-color: #162a3d;
  width: 20rem;
  height: 3rem;
  padding: 0 2rem;
  border-radius: 0.5rem;
}

@media (min-width: 768px) {
  .candidato__buscar {
    width: 20rem;
  }
}
@media (min-width: 1200px) {
  .candidato__buscar {
    width: 25rem;
  }
}
.buscar img {
  width: 2rem;
}

.candidato__buscar input {
  width: 100%;
  background-color: transparent;
  border-style: none;
  outline: none;
  color: #fff;
}

.candidato__buscar input::placeholder {
  font-size: 1.5rem;
}

.candidato__navegacion {
  display: none;
  gap: 3rem;
  align-items: center;
}

@media (min-width: 768px) {
  .candidato__navegacion {
    display: flex;
    gap: 5rem;
  }
}
.navegacion__opc a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 3rem;
}

.candidato__navegacion p {
  color: #707070;
  font-size: 1.2rem;
}

.candidato__navegacion a:hover > p {
  transition: all 1s ease;
  color: #fff;
}

.candidato__navegacion img {
  width: 2rem;
}

.navegacion__Perfil {
  border: #0C6432 2px solid;
  border-radius: 50%;
}

.navegacion__Perfil img {
  width: 3rem;
  height: 3rem;
  max-width: none;
  border-radius: 50%;
}

.header__mobile {
  /* display: flex; */
  height: 6rem;
  width: 100%;
  background-color: #13161C;
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 1;
  bottom: 0rem;
  left: 0;
}

.header__desktop {
  display: none;
}

.candidato__navegacion__mobile {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  gap: 7rem;
}

.navegacion__opc img {
  width: 50rem;
}

@media (min-width: 768px) {
  .header__mobile {
    display: none;
  }
  .header__desktop {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .navegacion__Perfil img {
    width: 4.5rem;
    height: 4.5rem;
  }
  .navegacion__opc img {
    width: 2.2rem;
  }
  .navegacion__opc p {
    font-size: 1.2rem;
  }
}
.perfilDesplegable {
  z-index: 1;
  position: fixed;
  padding: 1rem 2rem;
  bottom: 6rem;
  width: 30rem;
  height: 45rem;
  background-color: #fff;
  flex-direction: column;
  gap: 2rem;
  border-top-right-radius: 1rem;
}
@media (min-width: 768px) {
  .perfilDesplegable a {
    padding: 0.5rem;
  }
}
@media (min-width: 768px) {
  .perfilDesplegable {
    gap: 1rem;
    position: absolute;
    width: 30rem;
    height: auto;
    top: 6.5rem;
    right: 8rem;
    bottom: unset;
    border-bottom-right-radius: 1rem;
    border-bottom-left-radius: 1rem;
    border-top-right-radius: 0;
  }
}
@media (min-width: 1024px) {
  .perfilDesplegable {
    right: 15rem;
    height: auto;
  }
  .perfilDesplegable hr {
    display: block;
  }
}
.perfilDesplegable__perfil {
  display: flex;
  align-items: center;
  line-height: 1.5;
  gap: 1rem;
}
.perfilDesplegable__perfil img {
  border-radius: 50%;
  width: 5rem;
}
.perfilDesplegable__perfil h3 {
  font-weight: 600;
  padding: 0;
  margin: 0;
}
.perfilDesplegable nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .perfilDesplegable nav {
    text-align: left;
    gap: 1rem;
  }
}
.perfilDesplegable nav a {
  font-size: 1.6rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .perfilDesplegable nav a {
    font-size: 1.4rem;
  }
}
.perfilDesplegable nav a:hover {
  background-color: #0C6432;
}

/* Header */
.header {
  background-color: #13161C;
  height: 6.5rem;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.header__logo p {
  color: #fff;
  font-weight: 600;
  font-family: "Montserrat Alternates", sans-serif;
  font-size: 2rem;
  width: 20%;
}
.header__navegacion {
  display: flex;
  justify-content: space-between;
  width: 80%;
}
.header__navegacion a {
  color: gray;
  font-weight: 300;
  text-transform: uppercase;
  font-size: 1.4rem;
  margin-right: 2rem;
}

@media (min-width: 480px) {
  .header {
    padding: 2rem 5rem;
  }
  .header__logo p {
    font-size: 3rem;
  }
}
.navegacion__enlace:hover {
  color: #fff;
  transition: all 0.45s ease;
}

.navegacion__bloque1,
.navegacion__bloque2 {
  display: none;
}

.navegacion__bloque2 a:last-child {
  font-weight: 600;
  color: #fff;
}

.navegacion__bloque2 a:hover:last-child {
  color: #0C6432;
}

.navegacion__mobil img {
  width: 5rem;
  display: flex;
}

.header__navegacionMobile {
  position: fixed;
  background-color: #13161C;
  height: 100%;
  width: 70%;
  padding: 3rem 0;
  top: 0;
  right: 0;
}

.navegacion__enlace--cerrar img {
  width: 4rem;
