.card {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  border-radius: 1rem;
  position: relative;
  height: 20rem;
  color: #fff;
  transition: all 0.45s ease;
}
.card__des {
  color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
  z-index: 2;
  transition-delay: 0.1s;
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.card::before,
.card::after {
  background-color: rgba(0, 0, 0, 0.5);
  border-top: 32px solid rgba(0, 0, 0, 0.5);
  border-bottom: 32px solid rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  content: "";
  transition: all 0.3s ease;
  z-index: 1;
  opacity: 0;
  transform: scaleY(1);
}

.card:hover::before,
.card:hover::after {
  transform: scale(1);
  opacity: 1;
}

.card:hover > .card__contendido {
  display: none;
}

.card:hover > .card__des {
  opacity: 1;
}

