  /* margin: 20px; */
  background-color: transparent;
  border: #707070 solid 1px;
  box-shadow: 0 10px 20px -8px #c5d6d6;
  border-radius: 5px;
}

.button-cover:before {
  counter-increment: button-counter;
  /* content: counter(button-counter); */
  position: absolute;
  right: 0;
  bottom: 0;
  /* color: #d7e3e3; */
  font-size: 12px;
  line-height: 1;
  /* padding: 5px; */
}

.button-cover,
.knobs,
.layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.button {
  position: relative;
  top: 50%;
  width: 95%;
  height: 35px;
  margin: -20px auto 0 auto;
  overflow: hidden;
}

.button.r,
.button.r .layer {
  border-radius: 100px;
}

