}

.entrada__blog__card {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1rem;
  margin-top: 2rem;
  position: relative; /* Agrega posición relativa para poder aplicar el pseudo-elemento ::before */
  transition: transform 0.2s ease-in-out; /* Añade una transición para el efecto hover */
}

.entrada__blog__card:hover {
  cursor: pointer;
  transform: scale(1.05); /* Efecto de escala al hacer hover */
}

.entrada__blog__card::before {
  content: "";
  position: absolute;
  border: solid #0C6432 1px;
  height: 8rem;
  top: -1rem; /* Ajusta la posición del borde */
  left: -1rem; /* Ajusta la posición del borde */
}

