  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-image: url(../img/1646811898925.jpeg);
}
.prospectos__card__infoProspecto {
  height: 30rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  gap: 0.5rem; /* Ajustamos el espacio entre elementos */
}
.prospectos__card__infoProspecto img {
  margin-top: -5rem;
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
}
.prospectos__card:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Estilos para los párrafos dentro del .prospectos__card__infoProspecto */
.prospectos__card__infoProspecto p {
  margin: 0; /* Quitamos el margen por defecto para los párrafos */
}

p.infoProspecto__nombre {
  font-size: 1.8rem;
  font-weight: 600;
}

p.infoProspecto__puesto {
  font-size: 1.6rem;
}

p.infoProspecto__vacante {
  font-size: 1.6rem;
  color: #0C6432;
}

/* Estilos para el botón "Ver perfil" */
a.boton__verde__postulacion {
  background-color: #0C6432;
  color: #fff;
  text-decoration: none;
  padding: 1rem 1rem;
  border-radius: 0.3rem;
